#allwrap3{
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #232323;
  background-image: url(../images/cardgamebg.png);
  background-position: center;
  background-size: 100vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;



  .innerwrap{
    position: relative;
    outline: 1px solid #000000df;
    border-radius: 15px;
    max-width: 1200px;

    margin: 0 30px;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 50px;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.69);
    box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.3);
    // outline:  1px dotted red;
  
    .backbtm{
      color: #fff;
      position: absolute;
      left: 50px;
      top: 60px;
      outline: none;
      border:  none;
      cursor: pointer;
      width: 70px;
      height: 45px;
      font-size: 45px;
      background-color: #ffffff00;
      transition: 0.2s;
      &:hover{
        scale: 1.2;
        text-shadow: 2px 2px 4px black;
      }
      
    }
  
    
    .projname{
      width: 100%;
      text-align: center;
      margin-bottom: 40px;

      h1{
        // outline: 1px dotted red;
        // padding-top: 100px;
        font-size: 3rem;
        color: #fff;
        margin-bottom: 15px;
      }
      p{
        // outline: 1px dotted red;
        color: #fff;
        
      }
    }

    .desc{
      display: flex;
      height: 400px;
      color: #fff;
      text-shadow: 2px 2px 2px black;
      // margin-bottom: 10px;
      gap: 20px;

      .photo{
        border-radius: 15px;
        // background-color: #3d3d3d;
        display: flex;
        justify-content: center;
      align-items: center;
      outline: 1px solid #a9a9a937;
      flex: 0.8;
        box-shadow: 6px 6px 8px rgba(0, 0, 0, 0.424);
        height: 340px;     
        overflow: hidden;  

        video{
        width: 100%;
        height: auto;
        scale: 1;
        background-position: center;

       
      }
        }
      .text{
        // text-indent: 15px;
        position: relative;
        height: 340px;
        box-sizing: border-box;
        line-height: 1.3rem;
        word-break: keep-all;
        font-size: 1.2rem;
        padding: 10px;
        // outline: 1px dotted red;
        // background-color: rgba(240, 128, 128, 0.426);
        flex: .9;
        letter-spacing: -1.3px;
        p{
          font-weight: 200;
          margin-bottom: 20px;
        }
        span{font-weight: 700; color: rgb(255, 209, 60);}

        button{
          bottom: 0;
          position: absolute;
          margin-top: 80px;
          width: 100px;
          height: 30px;
          border: 0;
          outline: 0;
          background-color:rgba(190, 190, 190, 0.281);
          color: #fff;
          border-radius: 8px;
          box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.175);
          cursor: pointer;
          transition: 0.2s;

          &:hover{
            scale: 1.05;
          }
        }
      }

      }
        .btm{
          // width: 450px;
          margin: 0 auto;
        // outline: 1px dotted red;
          display: flex;
          color: #d6d6d6;
          font-weight: 200;
          font-size: 14px;

          .mainfunc{
            line-height: 25px;
            // margin-right: 20px;
            // outline: 1px dotted red;
            // width: 300px;
            flex: 1;
            & p:nth-child(1){
              font-size: 18px;
              font-weight: 600;
              text-decoration: underline;
              text-underline-offset: 6px;
              margin-bottom: 5px;
              color: #fff;
            }
          }
          .skill{
            line-height: 25px;
            // outline: 1px dotted red;  
            // width: 130px;
            flex: 1;
            & p:nth-child(1){
              font-size: 18px;
              font-weight: 600;
              text-decoration: underline;
              text-underline-offset: 6px;
              margin-bottom: 5px;
              color: #fff;
            }
          }
        }
  }

}

