.fade-in-text {
  opacity: 0;
  transition: opacity .8s ease-in-out;
}

.fade-in-text.visible {
  opacity: 1;
}

html,body{
  overflow-x: hidden;
}
*{margin: 0; padding: 0;}


#header{
  // outline: 1px dotted red;
  position: fixed;
  width: 100%; 
  height: 100vh;
  background-color: #00000015;
  z-index: 5;
  background-image: url(../images/paperbg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  // background-position: center;
  transition: 1s;
  // opacity: 0.2;

  .textwrap{  
    text-align: center;
    width: 100%;
    position: absolute;
    top: 20%; left: 50%;
    transform: translateX(-50%);
    // outline: 1px dotted red;
    transition: 0.3s;
    h1{
      text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.27);
      text-align: center;
      width: 100%;
      // outline: 1px dotted red;
      font-size: 20vh;
      font-weight: 700;
      transition: 0.3s;
    }
    p{ 
      font-weight: 700;
      transform: translateX(-50%);
      left: 50%;
      bottom: -15px;
      position: absolute;
      color: rgb(75, 75, 75);
      text-align: center;
      transition: 0.3s;
      font-size: 4vh;
    }
    span{
      position: absolute;
      bottom: 2px;
    }
}

#textwrap_two{
  text-shadow:none;
  text-align: center;
  width: 100%;
  position: absolute;
  top: 40%; left: 50%;
  transform: translateX(-50%);
  // outline: 1px dotted red;
  transition: 0.3s;
  color: black;
  font-weight: 400;
  font-size: 2vh;
  span{
    font-weight: 700;
  }

}
  #myphoto{
    //  outline: 1px dotted red;
    position: absolute;
    bottom: -55px;
    left: 50%;
    transform: translateX(-50%);
    width: 45vh;
    height: 55vh;
    background-image: url(../images/suhwan_black.png); 
    background-repeat: no-repeat;
    background-size: cover;
    // opacity: .1;

    .thispageis{
      font-weight: 300;
      text-align: center;
      // letter-spacing: -1px;
      display: inline-block;
      width: 300px;
      // outline: 1px dotted red;
      position: absolute;
      left: 50%;
      bottom: 70px;
      transform: translateX(-50%);
      color: #fff;
      font-size: 0.8rem;
      text-shadow: 2px 2px 2px black;
    }

  }
}
