.proj {
  // outline: 1px dotted red;
  width: 100%;
  max-width: 1300px;
  height: 350px;
  background-color: #2f2f2f;
  color: #fff;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.267);
  transition: 0.3s;
  display: flex;
  transform: translateX(-30px);
  margin: 0 30px; /* 양쪽 마진을 30px로 설정 */
  margin-bottom: 50px;
  position: relative;
/* 
  &:hover {
      scale: 1.01;

      .mainImg {
          transition: 0.3s;
          box-shadow: 8px 0px 10px rgba(0, 0, 0, 0.097);
      }
  } */

  .mainImg {
      flex: 1;
      height: 100%;
      background-color: rgba(65, 105, 225, 0.137);

      img {
          width: 100%;
          height: 100%;
          object-fit: cover;
      }
  }

  .projinner {
      
      flex: 2;
      padding: 50px;

      .btnallwrap{
        display: flex;
          position: absolute;
          bottom: 30px;
          gap : 15px;

          .detailpage{
        // position: absolute;
        bottom: 30px;
        font-weight: 500;
        font-size: 14px;
        // outline:  1px dotted red;
        padding: 10px;
        border-radius: 5px;
        background-color: rgba(65, 105, 225, 0.471);
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.461);
        transition: 0.2s;
        &:hover{
            scale: 1.1;
            
        }
    }
    .gitpage{
        // position: absolute;
        bottom: 30px;
        font-size: 14px;
        // outline:  1px dotted red;
        padding: 10px;
        border-radius: 5px;
        background-color: rgba(65, 105, 225, 0.471);
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.461);
        transition: 0.2s;
        &:hover{
            scale: 1.1;
            
        }
    }
}
      .text {
          p.name {
              margin-top: 10px;
              font-size: 2rem;
              margin-bottom: 30px;
          }

          p.detail {
              font-size: 1rem;
              line-height: 1.5rem;
              margin-top: 10px;
              font-weight: 100;
              word-break: keep-all;
          }
      }

      .use {
          margin-top: 20px;

          ul {
              display: flex;
              flex-wrap: wrap;

              li {
                  font-size: 1rem;
                  display: inline-block;
                  padding: 5px 10px;
                  height: auto;
                  line-height: 1.5rem;
                  text-align: center;
                  border-radius: 5px;
                  margin-right: 10px;
                  background-color: #ffffff1c;
                  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.067);
              }
          }
      }
  }
}

