@media screen and (max-width : 580px) {

    .proj {
        // outline: 1px dotted red;
        width: 100%;
        max-width: 500px!important;
        height: 300px;
        background-color: #2f2f2f;
        color: #fff;
        // overflow: hidden;
        border-radius: 15px;
        box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.267);
        transition: 0.3s;
        display: block;
        transform: translateX(-30px);
        margin: 0 30px; /* 양쪽 마진을 30px로 설정 */
        margin-bottom: 50px;
      

        .btnallwrap{
            .detailpage, .gitpage{
                display: none;
            }

        }
    /*     &:hover {
            scale: 1.01;
      
            .mainImg {
                transition: 0.3s;
                box-shadow: 8px 0px 10px rgba(0, 0, 0, 0.097);
            }
        } */
      
        .mainImg {
            flex: 1.5;
            height: 40%;
            background-color: rgba(65, 105, 225, 0.137);
      
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
      
        .projinner {
            // box-sizing: border-box;
            flex: 2;
            padding: 15px!important;
      
            .text {
                p.name {
                    margin-top: 5px!important;
                    font-size: 1.3rem!important;
                    margin-bottom: 9px;
                }
      
                p.detail {
                    // outline: 1px dotted red;
                    font-size: 0.75rem!important;
                    line-height: 1.1rem;
                    margin-top: 0px;
                    font-weight: 100;
                    word-break: keep-all;
                }
            }
      
            .use {
                margin-top: 10px;
      
                ul {
                    display: flex;
                    flex-wrap: wrap;
      
                    li {
                        font-size: 0.725rem;
                        display: inline-block;
                        padding: 0px 10px;
                        // height: auto;
                        line-height: 1.5rem;
                        text-align: center;
                        border-radius: 5px;
                        margin-right: 10px;
                        background-color: #ffffff1c;
                        box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.067);
                    }
                }
            }
        }
      }

    
}