@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap');

// fadein - out 효과
.fade-in-image {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.fade-in-image.visible {
  opacity: 1;
}

.colorchange {
  transition: 0.3s;
  color: #fff;
  opacity: 0.1;
}

.colorchange.visible {
  color: royalblue;
  opacity: 1;
}

#Body {
  position: absolute;
  top: 100vh;
  width: 100%;
  height: 4200px;
  z-index: 15;
  background-color: #232323;
  // box-shadow: 5px 5px 15px black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
    
  #about {
    position: relative;
    z-index: 9;
    margin-top: 50px;
    flex: 1;
    text-align: center;
    font-size: 4rem;
    color: #fff;
    font-weight: 500;
    margin-bottom: 40px;
  }

  #resultwrap {
    position: relative;
    display: flex;
    width: 100%;
    height: 610px;
    justify-content: center;
    // outline: 1px dotted red;
    
    #result {
      overflow: hidden;
      // outline:  1px dotted red;
      display: flex;
      color: #fff;
      margin: 0 30px;
      max-width: 1300px;
      height: 500px;
      // margin-bottom: 130px;
      border-radius: 15px;
      box-shadow: 15px 15px 10px rgba(0, 0, 0, 0.164);
      padding: 0px 20px 20px 20px;
      justify-content: center;
      box-sizing: border-box;
      background-color: #2f2f2f;
      z-index: 3;

      .resultphoto {
        margin-left: 50px;
        width: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: 5px 5px 10px rgba(0, 0, 0, 0.276);

        .resultphotoinner {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 15px;
          width: 300px;
          height: 400px;
          background-color: rgba(65, 105, 225, 0.151);
          background-image: url(../images/giphy.gif);
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }

      .resulttext {
        font-weight: 200;
        display: flex;
        flex-direction: column;
        letter-spacing: -1px;
        justify-content: center;
        text-align: center;
        padding: 50px;
        box-sizing: border-box;
        // outline: 1px dotted red;

        p {
          line-height: 40px;
          font-size: 1.6rem;
          word-break: keep-all;
          margin-bottom: 35px;
          text-align: center;
        }
      }
    }
  }

  .skilleduallwrap {
    width: 100%;
    display: flex;
    justify-content: center;
    // outline: 1px dotted red;

    #skillandeduall {
      position: relative;
      width: 1300px;
      display: flex;
      gap: 40px;
      margin: 0 30px;
      // justify-content: space-between;
      // align-items: center;
      // box-sizing: border-box;
      // outline: 1px dotted red;

      #skillallwrap {
        max-width: 630px;
        flex: 1;
        // outline: 1px dotted red;

        .skillandtools {
          text-align: center;
          font-size: 4rem;
          color: #fff;
          font-weight: 500;
          margin-bottom: 40px;
        }

        #skillwrap {
          // width: 630px;
          max-width: 630px;
          // outline: 1px dotted red;
          height: 450px;
          border-radius: 15px;
          margin: 0 auto;
          color: #fff;
          padding: 40px;
          // padding-left: 120px;
          box-sizing: border-box;
          background-color: #2f2f2f;
          box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);

          .skill {
            // outline: 1px dotted red;
            display: flex;
            justify-content: center;
            // width: 500px;

            ul {
              margin-top: 20px;
              display: flex;
              flex-direction: column;

              li {
                p {}

                .skillicon {
                  // outline: 1px dotted red;
                  position: relative;
                  margin-right: 30px;
                  // padding-left: 40px;
                  z-index: 39;

                  ul {
                    // margin: 0 auto;
                    position: relative;
                    z-index: 50;
                    display: flex;
                    flex-direction: row;

                    li {
                      position: inherit;
                      z-index: 40;
                      margin-bottom: 20px;
                      flex-direction: row;
                      cursor: pointer;
                      font-size: 25px;
                      border-radius: 15px;
                      text-align: center;
                      line-height: 40px;
                      margin-right: 10px;
                      width: 70px;
                      height: 45px;
                      display: inline-block;
                      background-color: #404040;
                      transition: 0.2s;
                      box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.238);

                      &:hover {
                        scale: 1.1;
                      }

                      &.lastli {
                        position: absolute;
                        z-index: -1;

                        & ul {
                          position: absolute;
                          z-index: 0;
                          left: 0;

                          & li {
                            position: absolute;
                            z-index: 10;
                            border-radius: 15px;
                            background-color: rgb(97, 97, 97);
                            top: -120px;
                            width: 500px;
                            height: 440px;

                            &:hover {
                              scale: 1;
                              cursor: unset;
                            }
                          }
                        }
                      }
                    }
                  }
                }

                p {
                  font-weight: 500;
                  font-size: 25px;
                }
              }
            }
          }
        }
      }

      .edctallwrap {
        width: 100%;
        flex: 1;
        // max-width: 630px;
        // outline: 1px dotted red;

        #eduandcerti {
          // width: 630px;
          // max-width: 630px;
          z-index: 1;
          flex: 1;
          // outline: 1px dotted red;

          .edutext {
            text-align: center;
            font-size: 4rem;
            color: #fff;
            font-weight: 500;
            margin-bottom: 40px;
          }

          .edubox {
            // max-width: 630px;
            height: 450px;
            border-radius: 15px;
            margin: 0 auto;
            color: #fff;
            padding: 60px;
            box-sizing: border-box;
            background-color: #2f2f2f;
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);

            .education {
              border-bottom: 1px solid #fff;

              p {
                text-align: left;
                font-size: 2.5rem;
                color: #fff;
                font-weight: 500;
                margin-bottom: 20px;
              }

              ul {
                margin-bottom: 30px;

                li {
                  line-height: 40px;
                  color: #b0b0b0;
                }
              }
            }

            .certificate {
              margin-top: 30px;

              p {
                text-align: left;
                font-size: 2.5rem;
                color: #fff;
                font-weight: 500;
                margin-bottom: 20px;
              }

              ul {
                li {
                  line-height: 40px;
                  color: #b0b0b0;
                }
              }
            }
          }
        }
      }
    }
  }

  #skilldetails {
    line-height: 30px;
    font-size: 22px;
    padding-left: 50px;
    padding-right: 50px;
    box-sizing: border-box;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 265px;
    left: 80px;
    width: 450px;
    height: 0;
    max-height: 0;
    overflow: hidden;
    background-color: #fff;
    z-index: 90;
    border-radius: 0px 0px 15px 15px;
    box-shadow: inset 0px 10px 10px rgba(0, 0, 0, 0.341);
    transition: 0.2s;
    background-image: url(../images/paperdesc.jpg);
    background-position: center;
    background-size: cover;

    &.slide-down {
      line-height: 30px;
      font-size: 22px;
      box-sizing: border-box;
      padding: 50px;
      height: 150px;
      max-height: 200px;
    }
  }

  #skilldetails::before {
    content: '';
    opacity: 0.3;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: #00000000;
  }

  #skilldetails2 {
    line-height: 30px;
    font-size: 22px;
    padding-left: 50px;
    padding-right: 50px;
    box-sizing: border-box;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 373px;
    left: 80px;
    width: 450px;
    height: 0;
    max-height: 0;
    overflow: hidden;
    background-color: #fff;
    z-index: 90;
    border-radius: 0px 0px 15px 15px;
    box-shadow: inset 0px 10px 10px rgba(0, 0, 0, 0.341);
    transition: 0.2s;
    background-image: url(../images/paperdesc.jpg);
    background-position: center;
    background-size: cover;

    &.slide-down2 {
      font-size: 22px;
      box-sizing: border-box;
      padding: 50px;
      height: 150px;
      max-height: 200px;
    }
  }

  #skilldetails2::before {
    content: '';
    opacity: 0.3;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: #00000000;
  }

  .portwrapall {
    #portwrap {
      // outline: 1px dotted red;
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      padding: 0 30px; /* 양쪽에 30px padding 추가 */
      box-sizing: border-box;

      #port {
        gap: 50px;
        width: 100%;
        max-width: 1300px;
        margin-bottom: 200px;
        box-sizing: border-box;

        #project {
          margin-top: 100px;
          text-align: center;
          font-size: 4rem;
          color: #fff;
          font-weight: 500;
          margin-bottom: 40px;
          word-break: keep-all;

          ul{
            display: flex;
            justify-content: center;
            li{
              &:nth-child(2){
                font-size: 10px;
              }
            }
          }
        }

        .display {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          word-break: keep-all;
          // outline: 1px dotted red;
        }
      }
    }
  }
}

