                        cursor: pointer;
                        font-size: 25px;
                        border-radius: 15px;
                        text-align: center;
                        line-height: 40px;
                        margin-right: 10px;
                        width: 60px;
                        height: 40px;
                        display: inline-block;
                        background-color: #404040;
                        transition: 0.2s;
                        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.238);

                        &:hover {
                          scale: 1.1;
                        }

                        &.lastli {
                          position: absolute;
                          z-index: -1;

                          & ul {
                            position: absolute;
                            z-index: 0;
                            left: 0;

                            & li {
                              position: absolute;
                              z-index: 10;
                              border-radius: 15px;
                              background-color: rgb(97, 97, 97);
                              top: -120px;
                              width: 500px;
                              height: 440px;

                              &:hover {
                                scale: 1;
                                cursor: unset;
                              }
                            }
                          }
                        }
                      }
                    }
                  }

                  p {
                    font-weight: 500;
                    font-size: 25px;
                  }
                }
              }
            }
          }
        }
      }

      .edctallwrap {
        #eduandcerti {
