@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap');

@media screen and (max-width: 500px) {
  #Body {
    left: 0;
    right: 0;
    max-width: 500px !important;
    height: 3500px!important;

    #about {
      position: relative;
      z-index: 9;
      margin-top: 25px !important;;
      flex: 1;
      text-align: center;
      font-size: 2.5rem;
      color: #fff;
      font-weight: 500;
      margin-bottom: 25px !important;;
    }

    #skillandeduall {
      justify-content: center;
      align-items: center;
      flex-direction: column;
      // gap: 20px;
    }

    #result {
      // outline: 1px dotted red;
      display: flex;
      color: #fff;
      margin: 0 30px;
      max-width: 100%;
      height: 350px !important;;
      border-radius: 15px;
      box-shadow: 15px 15px 10px rgba(0, 0, 0, 0.164);
      padding: 0px 20px 20px 20px ;
      justify-content: center;
      box-sizing: border-box;
      background-color: #2f2f2f;
      z-index: 3;

      .resultphoto {

        width: 100% !important;;
        display: flex !important;
        justify-content: center !important;
        align-items: center!important;
        box-sizing: border-box;

        .resultphotoinner {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 15px;
          width: 100%;
          height: 150px!important;
          background-color: rgba(65, 105, 225, 0.151);
          background-image: url(../images/giphy.gif);
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }

      .resulttext {
        font-weight: 200;
        display: flex;
        flex-direction: column;
        letter-spacing: -1px;
        justify-content: center;
        text-align: center;
        padding: 0px !important;
        box-sizing: border-box;
        margin-top: 10px !important;;

        p {
          // outline: 1px dotted red;
          line-height: 1.3rem !important;;
          font-size: 12px !important;
          margin-bottom: 10px !important;;
          text-align: center;
        }
      }
    }

    #resultwrap {
      // outline: 1px dotted red;
      margin-bottom: -200px !important;
    }

    .skilleduallwrap {
      max-width: 500px!important;
      display: flex;
      justify-content: center;

      #skillandeduall {
        position: relative;
        width: 100%;
        max-width: 500px!important;
        display: inline-block;
        margin: 0 30px;
        
        #skillallwrap {
          width: 100%;
          max-width: 500px !important;
          margin: 0 auto;
          
          .skillandtools {
            text-align: center;
            font-size: 2.5rem;
            color: #fff;
            font-weight: 500;
            margin-bottom: 25px!important;
          }
          
          #skillwrap {
            // outline:  1px dotted red;
            max-width: 500px!important;
            display: flex;
            margin: 0 30px;
            height: 330px!important;
            border-radius: 15px;
            display: flex;
            color: #fff;
            padding:30px;
            padding-top: 0px;
            background-color: #2f2f2f;
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);
            box-sizing: border-box;

            .skill {
              display: flex;
              justify-content: center;
              // outline: 1px dotted red;
              width: 500px!important;
              ul {
                margin-top: 20px;
                display: flex;
                width: 200px;
                // outline: 1px dotted red;
                
                li {
                  .skillicon {
                    // outline: 1px dotted red;
                    position: relative;
                    z-index: 39;

                    &>ul {
                      position: relative;
                      z-index: 50;
                      display: flex;
                      flex-direction: row;

                      li {
                        position: inherit;
                        z-index: 40;
                        margin-bottom: 20px;
                        flex-direction: row;
                        cursor: pointer;
                        font-size: 25px;
                        border-radius: 15px;
                        text-align: center;
                        line-height: 40px;
                        margin-right: 10px;
                        width: 40px!important;
                        height: 40px!important;
                        display: inline-block;
                        background-color: #404040;
                        transition: 0.2s;
                        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.238);

                        &:hover {
                          scale: 1.1;
                        }

                        &.lastli {
                          position: absolute;
                          z-index: -1;

                          & ul {
                            position: absolute;
                            z-index: 0;
                            left: 0;

                            & li {
                              position: absolute;
                              z-index: 10;
                              border-radius: 15px;
                              background-color: rgb(97, 97, 97);
                              top: -120px;
                              max-width: 500px!important;
                              height: 440px;

                              &:hover {
                                scale: 1;
                                cursor: unset;
                              }
                            }
                          }
                        }
                      }
                    }
                  }

                  p {
                    font-weight: 500;
                    font-size: 25px;
                  }
                }
              }
            }
          }
        }
      }

      .edctallwrap {
        #eduandcerti {
          margin: 0 30px;
          padding: 0px;
          // outline: 1px dotted red;

          .edutext {
            max-width: 500px!important;
            font-size: 2.6rem !important;
            color: #fff;
            font-weight: 500;
            margin-top: 25px;
            margin-bottom: 25px!important;
            // outline: 1px dotted red;
          }

          .edubox {
            // outline: 1px dotted red;
            height: 300px!important;
            border-radius: 15px;
            color: #fff;
            padding: 30px!important;
            background-color: #2f2f2f;
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);

            .education {
              border-bottom: 1px solid #fff;

              p {
                font-size: 2.2rem!important;
                color: #fff;
                font-weight: 500;
                margin-bottom: 10px!important;
              }

              ul {
                margin-bottom: 10px!important;

                li {
                  line-height: 30px!important;
                  color: #b0b0b0;
                }
              }
            }

            .certificate {
              margin-top: 20px!important;

              p {
                text-align: left;
                font-size: 2.2rem!important;
                color: #fff;
                font-weight: 500;
                margin-bottom: 10px !important;
              }

              ul {
                li {
                  line-height: 30px !important;
                  color: #b0b0b0;
                }
              }
            }
          }
        }
      }

      #portwrap {
        #port {
          width: 100%;
          

          .display {
            width: 100%;
            flex-direction: column;
          }
        }
      }
    }
  }
}
