
.all{

  
  width: 100%;
  height: 100vh;
  position:relative;
  color : #fff;
  z-index : 9;
  // background-image: url(${paperbg});
  background-repeat: no-repeat;
  background-size: cover;

  #textwrap{
    
    
    width: 100%;
    height: 100vh;
    position:relative;
    color : #fff;
    z-index : 9;
    // background-image: url(${paperbg});
    background-repeat: no-repeat;
    background-size: cover;
    
    .textinnertop{
      text-align: center;
      width: 100%;
      position: absolute;
      top: 20%; left: 50%;
      transform: translateX(-50%);
      // outline: 1px dotted red;
      transition: 0.3s;
      
      p:nth-child(1){
        text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.27);
        text-align: center;
        width: 100%;
        // outline: 1px dotted red;
        font-size: 19vh;
        letter-spacing : -12px;
        font-weight: 500;
        transition: 0.3s;
      }
      p:nth-child(2){
        text-align: center;
        width: 100%;
        margin-top: 35px;
        // outline: 1px dotted red;
        font-size: 4vh;
        font-weight: 400;
        transition: 0.3s;
        color: rgb(75, 75, 75);
      }
    }
    .textinnerbtm{
      position: absolute;
      top : 55%;
      left:50%;
      transform: translateX(-50%);
      text-align: center;
      width: 100%;
      // outline: 1px dotted red;
      font-size: 2.3vh;
      font-weight: 400;
      transition: 0.3s;
      color: black;

      p{
        margin-bottom: 8px;
      }
    }
    
  }
  
  #contact{
    position:absolute;
    top:75%;
    left:50%;
    transform: translateX(-50%);
    color:black;
    
    
    p{
      font-size : 2.6vh;
      margin-bottom : 20px;
      text-align : center;
      span{
        font-weight:700;
      }
    }
  }
}
  
  
