.fade-in-text {
    opacity: 0;
    transition: opacity .8s ease-in-out;
  }
  
  .fade-in-text.visible {
    opacity: 1;
  }


.menuwrap{

    position:relative;
width : 100%;
height:100vh;

#fixedbox{
    position: fixed;
    right : 40px;
    bottom:80px;
    width : 50px;
    height : 50px;
    display: block;
    z-index:999;
    color: rgb(124, 124, 124);

ul{
        li{
            font-size:30px;
            text-align : center;
            line-height : 50px;
            width : 50px;
            height : 50px;
            // outline : 1px dotted red;
            margin-bottom: 10px;

            &:nth-child(1){
                border-radius: 50%;
                cursor: pointer;
                color: rgb(124, 124, 124);
                text-shadow: 3px 3px 2px rgba(0, 0, 0, 0.093);
                // background-color: #42424232;
            }
            a{
                display: block;
                border-radius: 15px;
                background-color : #d2d2d222;
                box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.272);
            }
        }

    }
}
}

@media screen and (max-width : 780px) {
    

    .menuwrap{

        position:relative;
    width : 100%;
    height:100vh;
    
    #fixedbox{
        position: fixed;
        right : 40px;
        bottom:110px;
        width : 50px;
        height : 100px;
        display: block;
        z-index:999;
        color: rgb(124, 124, 124);
    
    ul{
        position: relative;
            li{
                font-size:30px;
                text-align : center;
                line-height : 50px;
                width : 50px;
                height : 50px;
                // outline : 1px dotted red;
                margin-bottom: 10px;

                &:nth-child(2) , &:nth-child(3){
                    display: none;
                }
    
                &:nth-child(1){
                    position:  absolute;
                    border-radius: 10px;
                    right: -20px;
                    bottom: -200px;
                    cursor: pointer;
                    color: rgb(124, 124, 124);
                    text-shadow: 3px 3px 2px rgba(0, 0, 0, 0.093);
                    background-color: #cacaca2a;
                }
                a{
                    display: block;
                    border-radius: 15px;
                    background-color : #d2d2d222;
                    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.272);
                }
            }
    
        }
    }
    }
}